import React from 'react'
import ShapeRight from './../../assets/temp/shape-right.png'
import ShapeLeft from './../../assets/temp/shape-left.png'
import MobileShape1 from './../../assets/temp/mobile-shape1.png'
import MobileShape2 from './../../assets/temp/mobile-shape2.png'
import MobileShape3 from './../../assets/temp/mobile-shape3.png'
import Gallery from './Gallery'
function Hero() {
    return (
        <div className='hero' >
            <img className='hero__shape-right' src={ShapeRight} alt='Zdjcie Natalii Zaraskiej' />
            <img className='hero__shape-left' src={ShapeLeft} alt='Dekoracja na stronie' />
            <picture className="mobile-hero" >
                <img src={MobileShape3} alt='Zdjcie Natalii Zaraskiej'/>
            </picture>
            <div className='hero__intro'>
                    <h2 className='intro__name'>Natalia</h2>
                    <h2 className='intro__surname'>Zaraska</h2>
            </div>
            <div className="hero__info">
                <p className='info-left'>Jestem przekonana, e masz w sobie potencja, unikatowy zestaw zasobw, ktry raz odkryty pozwoli Tobie realizowa wszystko, czego tylko zapragniesz.</p>
                <div className="info-shape"></div>
                <p className='info-right'>Wiem, e Twoja budzca si wiadomo doprowadzi Ciebie tam, gdzie podasz. Spord prawie 8 mld. ludzi na Ziemi jeste niepowtarzaln istot. Czego potrzebujesz, aby sta si tym, kim chcesz? Robi to co chcesz i myle jak chcesz?</p>
            </div>
            <div className="hero__question">
                <p className='question__text'>Czego potrzebujesz, aby sta si tym kim chcesz? <br></br> Robi to co chcesz i myle jak chcesz?</p>
            </div>
            <div className='hero__gallery'>
                <div className='gallery__header'>
                    <h3>Galeria</h3>
                </div>
                <div className="gallery__gallery-container">
                    <div className="gallery-container__photos">
                        <Gallery />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Hero
