import React from 'react'
import AboutUp from './../../assets/temp/about-up.png'
import MobileAboutUp from './../../assets/temp/mobile-about-up.png'
import AboutShape from './../../assets/temp/about-shape.png'
import MobileAboutDown from './../../assets/temp/mobile-about-down2.png'
import AboutDown from './../../assets/temp/about-down2.png'
import MobileAboutDecoration from './../../assets/temp/mobile-about-decoration.png'
import Arrow from './../../assets/temp/arrow.svg'
import BrainyLogo from './../../assets/temp/brainy.png'
import ForeverLogo from './../../assets/temp/forever.png'
function AboutContent() {
    return (
        <div className="about">
                <div className="about__about-section-first">
                    <h1 className='about__section-first__title'>O MNIE</h1>
                    <div className="about__section-first__container">
                        <div className="container__img">
                            <img src={AboutUp} alt="Zdjcie Natalii Zaraskiej"/>
                        </div>
                        <div className="container__shape--img">
                            <img src={AboutShape} alt="Ozdobny ksztat na stronie"/>
                        </div>
                        <div data-aos="fade-down" className="container__intro">
                            <h2 className='intro__header'>Cze</h2>
                            <p className="intro__text"> Myl, e zanim zaczniemy wspprac, chcesz si dowiedzie kim jestem. W 2001r. zdobyam tytu mgr pedagogiki ze specjalizacj resocjalizacja i rozpoczam prac jako przewodnik modych ludzi w szkole. By to czas niezwyky, kiedy mogam rozwija swoje pasje, wsppracowa, tworzy projekty i zdobywa wyksztacenie. Ukoczyam studia podyplomowe w zakresie doradztwa zawodowego, pomocy psychologicznej oraz psychologii pozytywnej. Mojej dzisiejszej drodze zawodowej kierunek wyznaczy coaching. Coachem zostaam w 2017r. </p>
                        </div>
                        <div className="container__description">
                            <p data-aos="fade-right" className='description__text'>Ten przeomowy moment sprawi, e poczyam ca dotychczasow wiedz, aby uczy si od najlepszych i stworzy przestrze, w ktrej take Ty bdziesz budzi swoj wiadomo. Zostaam take facylitatorem Job Craftingu, gdy wiem, jak wane jest poczucie sensu i zaangaowania w miejscu pracy.</p>
                            <div data-aos="fade-left" className="description__img">
                                <img src={MobileAboutUp} alt="Zdjcie Natalii Zaraskiej"/>
                            </div>
                        </div>
                        <div className="container__dot"></div>
                    </div>
                </div>
                <div className="about__about-section-second">
                    <div data-aos="fade-right" className="about__section-second__mentor">
                        <p className="mentor__text">Moimi mentorami s <bold> Paul Martinelli </bold> oraz <bold> Joe Dispenza </bold>. To od nich czerpi wiedz, to oni nadaj kierunek mojej pracy. Jestem trenerem, mwc i coachem <bold> The John Maxwell Team </bold>, midzynarodowej firmy, wywodzcej si ze Stanw Zjednoczonych, obecnie najwikszej organizacji szkolcej w zakresie przywdztwa i komunikacji na wiecie, posugujcej si autorskimi programami opracowanymi przez dr Johna C. Maxwella. </p>
                            <div className="mentor__link">
                                <a href='//wp.pl'>Wicej o John Maxwell Team</a>
                                <img src={Arrow} alt="Ozdoba - strzaka"/>
                            </div>
                    </div>
                    <p data-aos="fade-up" className="about__section-second__header">Ponadto wsppracuj jeszcze z dwoma innymi midzynarodowymi firmami:</p>
                    <div className="about__section-second__buisnesses">
                        <div data-aos="fade-right" className="buisnesses__buisness">
                            <div className="buisness__logo">
                                <img src={BrainyLogo} alt="Logo projektu Brainy" />

                            </div>
                            <h2 className="buisness__header">BRAINY</h2>
                            <p className="buisness__text">Brainy jest symbolem edukacji przyszoci. Pionierem, ktry napdza wiara w si genialnego umysu dziecka. Innowatorem w zakresie wykorzystania siy ludzkiego mzgu. Jestem wacicielk  Akademii Brainy - Akademii Mistrzw wiata, wierz bowiem, e  Lepszy Ucze to Lepszy Czowiek i Lepszy wiat.</p>
                            <div className="buisness__link">
                                <a>wicej o Brainy</a>
                                <img src={Arrow} alt='Ozdoba - strzaka' />
                            </div>
                        </div>
                        <img  src={MobileAboutDecoration} alt='Ksztat dekoracyjny' className="buisness__decoration" />
                        <div data-aos="fade-left" className="buisnesses__buisness">
                            <div className="buisness__logo">
                                <img src={ForeverLogo} alt="Logo projektu Forever" />
                            </div>
                            <h2 className="buisness__header">FOREVER</h2>
                            <p className="buisness__text">Forever Living Products, ktra od 1978 roku angauje si w wyszukiwanie najlepszych rde zdrowia i urody w naturze, by dzieli si nimi ze wiatem. Pasj firmy jest pomaganie innym by wyglda i czu si lepiej. Do Forever doprowadziy mnie moje wartoci. Prowadzc swj zesp ucz jak by wolnym finansowo, y zdrowo i ekologicznie. </p>
                            <div className="buisness__link">
                                <a>wicej o Forever</a>
                                <img src={Arrow} alt='Ozdoba - strzaka' />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="about__about-section-third">
                    <div data-aos="fade-up" className='about__section-third__text'>
                        <p className="text__text-up">Widziaam i dowiadczyam niezwykych przemian, widziaam jak dokonuj si cuda. Jak niewiele potrzeba, aby czowiek odnalaz kierunek. Znalaz statek i odci liny. Widziaam podrnikw, ktrzy odwayli si marzy i wyruszyli w rejs.</p>
                        <p className="text__text-down">Wszystko bowiem zaczyna si od wiary, znalezienia mapy, pierwszego kroku i obudzonej wiadomoci.</p>
                    </div>
                    <h2 className="about__section-third__header">Jeste ciekawy/a?</h2>
                    <img className='about__section-third__img--mobile' src={MobileAboutDown} alt="Zdjcie Natalii Zaraskiej"/>
                    <img className='about__section-third__img' src={AboutDown} alt="Zdjcie Natalii Zaraskiej"/>
                </div>
            </div>
    )
}

export default AboutContent
