import React from 'react'
import TopBar from '../Nav/TopBar'
import Logo1 from './../../assets/temp/offer-logo1.png'
import Logo2 from './../../assets/temp/offer-logo2.png'
import Logo3 from './../../assets/temp/offer-logo3.png'
import Logo4 from './../../assets/temp/offer-logo4.png'
import Logo5 from './../../assets/temp/offer-logo5.png'
import OfferShape1Section1 from './../../assets/temp/offer-shape1-section1.png'
import OfferShape1Section2 from './../../assets/temp/offer-shape1-section2.png'
import OfferShape1Section3 from './../../assets/temp/offer-shape1-section3.png'
import OfferShape2Section3 from './../../assets/temp/offer-shape2-section3.png'
import OfferShape1Section4 from './../../assets/temp/offer-shape1-section4.png'
import OfferShape2Section4 from './../../assets/temp/offer-shape2-section4.png'
import OfferShape1Section5 from './../../assets/temp/offer-shape1-section5.png'

import MobileOfferShape1Section1 from './../../assets/temp/mobile-offer-shape1-section1.png'
import MobileOfferShape2Section1 from './../../assets/temp/mobile-offer-shape2-section1.png'
import MobileOfferShape1Section2 from './../../assets/temp/mobile-offer-shape1-section2.png'
import MobileOfferShape2Section2 from './../../assets/temp/mobile-offer-shape2-section2.png'
import MobileOfferShape1Section3 from './../../assets/temp/mobile-offer-shape1-section3.png'
import MobileOfferShape2Section3 from './../../assets/temp/mobile-offer-shape2-section3.png'
import MobileOfferShape1Section4 from './../../assets/temp/mobile-offer-shape1-section4.png'
import MobileOfferShape1Section5 from './../../assets/temp/mobile-offer-shape1-section5.png'
import ProjectLogo1 from './../../assets/temp/offer-project-logo1.png'
import ProjectLogo2 from './../../assets/temp/offer-project-logo2.png'
import ProjectLogo3 from './../../assets/temp/offer-project-logo3.png'
import ProjectLogo4 from './../../assets/temp/offer-project-logo4.png'

function Offer() {
    return (
        <>
            <TopBar />
            <div className="offer">
                <div className="offer__interval interval-one"></div>

                <div className="offer__offer-section offer-section-one">
                    <img className='offer-section__one-mobile-img1 mobile-img' src={MobileOfferShape1Section1} alt="Ozdobny ksztat"/>
                    <img className='offer-section__one-mobile-img2 mobile-img' src={MobileOfferShape2Section1} alt="Ozdobny ksztat"/>
                    <img className='offer-section__one-img' src={OfferShape1Section1} alt="Ozdobny ksztat"/>
                    <h1 className="offer-section__header">LIVE COACHING</h1>
                    <div data-aos="fade-down" className="offer-section__logo">
                        <img src={Logo1} alt="Ozdobne logo"/>
                    </div>
                    <div className="offer-section__p-one">
                        <p data-aos="fade-left" className="p-one__header">Jest form rozwoju osobistego.</p>
                        <p data-aos="fade-left" className="p-one__text">Podczas sesji rozwijasz te sfery ycia, ktrymi chcesz si zaj, okrelasz cele i sposoby realizacji. Form cochingu moe by wiele. To Ty ustalasz czym chcesz si zaj, a ja towarzysz Tobie w tej podry.</p>
                    </div>
                    <p data-aos="fade-right" className="offer-section__p-two">Podczas sesji bdziemy sami/same. <br></br> Dostosuj si do Ciebie i bd uwanie dotrzymywa Tobie kroku. <br></br>  Wykorzystam zarwno znane i powszechnie stosowane metody <br></br> coachingowe, jak i wasne, wypracowane osobistym dowiadczeniem. </p>
                    <div className="offer-section__dot"></div>
                    <p data-aos="fade-right" className="offer-section__p-three">Na pierwszym spotkaniu poznam Twoje potrzeby i moliwoci. <br></br> Sprawdzimy czy chcemy ze sob wsppracowa. <br></br> To spotkanie bdzie niezobowizujce. <br></br> To Ty podejmiesz decyzj, czy chcesz ze mn wsppracowa. </p>
                    <div data-aos="fade-left" className="offer-section__p-four">
                        <p className="p-four__header">Dla kogo coaching? </p>
                        <p className="p-four__text">Dla wszystkich, ktrzy chc wiadomie wpywa na swoje ycie, dla tych, ktrzy chc si rozwija i potrzebuj zmiany, dla tych, ktrzy poszukuj siebie.</p>
                    </div>
                </div>
                <div className="offer__interval"></div>
                <div className="offer__offer-section offer-section-two">
                    <img className='offer-section__two-mobile-img1 mobile-img' src={MobileOfferShape1Section2} alt="Ozdobny ksztat"/>
                    <img className='offer-section__two-mobile-img2 mobile-img' src={MobileOfferShape2Section2} alt="Ozdobny ksztat"/>
                    <img className='offer-section__two-img1 ' src={OfferShape1Section2} alt="Ozdobny ksztat"/>
                    <h1 className="offer-section__header">COACHING BIZNESOWY</h1>
                    <div data-aos="fade-right" className="offer-section__logo">
                        <img src={Logo2} alt="Ozdobne logo"/>
                    </div>
                    <div data-aos="fade-right" className="offer-section__p-one">
                        <p className="p-one__header">To odmiana coachingu wspierajca organizacje w rozwoju.</p>
                        <p className="p-one__text">Wspiera przedsibiorcw / pracownikw w pracy nad celami <br></br> i wartociami firmy. Dziki sprawdzonym narzdziom <br></br> coachingowym, wdraanym w organizacje jako podstawowe <br></br> i kluczowe narzdzie do wspierania pracy <br></br> nad celami i deniami firmy, uzyskujemy efekt wietnie <br></br> zarzdzanym czasem i zasobami przedsibiorstwa przy <br></br> ograniczonych nakadach finansowych.</p>
                        <p className="p-one__text-mobile">Wspiera przedsibiorcw / pracownikw w pracy nad celami i wartociami firmy. Dziki sprawdzonym narzdziom coachingowym, wdraanym w organizacje jako podstawowe i kluczowe narzdzie do wspierania pracy nad celami i deniami firmy, uzyskujemy efekt wietnie zarzdzanym czasem i zasobami przedsibiorstwa przy ograniczonych nakadach finansowych.</p>
                    </div>
                    <div className="offer-section__dot"></div>
                    <p data-aos="fade-left" className="offer-section__p-two">Jeli chodzi o przykady obszarw zastosowa coachingu biznesowego, staje si on skutecznym narzdziem w przypadku gdy mamy do czynienia z: brakiem wystarczajcych umiejtnoci przywdczych oraz interpersonalnych, niepowodzeniem w tworzeniu zespou,  problemami z morale pracownikw w organizacji, tak by odpowiaday one oczekiwaniom organizacji i utrzymaniu zaangaowania pracownikw, wsparciem procesu wprowadzania zmian i zarzdzania zmianami w organizacji, wyznaczaniem celw i osiganiem sukcesw, wyborem i rozwijaniem kariery zawodowej, radzeniem sobie ze stresem i zapobieganie wypaleniu zawodowemu,  doskonaleniem umiejtnoci przywdczych, podejmowaniem nowych wyzwa (osobistych i zawodowych), uwalnianiem si od ogranicze,  automotywowaniem i motywowaniem innych itd.</p>
                    <div data-aos="fade-up" className="offer-section__p-three">
                        <p className="p-three__header">W coachingu bizsnesowym to waciciele firm ustalaj cel procesowy pracownikw. </p>
                    </div>
                </div>
                <div className="offer__interval interval-two"></div>
                <div className="offer__offer-section offer-section-three">
                    <img className='offer-section__three-mobile-img1 mobile-img' src={MobileOfferShape1Section3} alt="Ozdobny ksztat"/>
                    <img className='offer-section__three-mobile-img2 mobile-img' src={MobileOfferShape2Section3} alt="Ozdobny ksztat"/>
                    <img className='offer-section__three-img1' src={OfferShape1Section3} alt="Ozdobny ksztat"/>
                    <h1 className="offer-section__header">WARSZTATY JOB CRAFTING</h1>
                    <div data-aos="fade-left" className="offer-section__logo">
                        <img src={Logo3} alt="Ozdobne logo"/>
                    </div>
                    <p className="offer-section__p-one">
                        <p data-aos="fade-right" className="p-one__text">Wychodzc naprzeciw dynamicznej sytuacji na rynku pracy, <br></br> trudnociom z tym zwizanych zapraszam na warsztaty <br></br> Job Crafting czyli</p>
                        <p data-aos="fade-left" className="p-one__header">nowe metody budowania <br></br> zaangaowania <br></br> i poczucia sensu pracy.</p>
                    </p>
                    <p data-aos="fade-right" className="offer-section__p-two">Job crafting jest strategi, wyaniajc stan faktyczny - co jest i co mogoby by, nie skupia si na tym co jest ze i procesem czenia pracy z tosamoci osobow - to kim jeste, faktycznie ma znaczenie, kiedy jeste w pracy. Job crafting generuje zmiany w znaczeniu samej pracy, jak te jej tosamoci, a take odmienia znaczenie samego pojcia "praca" poprzez przesunicia w poszczeglnych zadaniach lub powizaniach, na drodze zmian, ktre pozwalaj pracownikowi na przekierowanie celu pracy oraz zmienienie sposobu jej odbierania. Psychologiczny sens pracy wyania si, kiedy ludzie czuj, e jest ona warta ich wysiku. Wykorzystywanie w pracy swoich mocnych stron i dziaanie zgodnie z wartociami, wzmacnia poczucie wasnej skutecznoci i pozwala widzie sens w swoich dziaaniach.</p>
                    <div data-aos="fade-left" className="offer-section__list">
                        <p>Korzyci pynce z job craftingu:</p>
                        <ul>
                            <li>wzrost satysfakcji</li>
                            <li>wzrost zaangaowania</li>
                            <li>wzrost zaangaowania</li>
                            <li>lepsze radzenie sobie ze zmianami</li>
                            <li>spostrzeganie wikszego sensu pracy</li>
                            <li>wzrost efektywnoci</li>
                        </ul>
                    </div>
                    <div  className="offer-section__p-three">
                        <img className='offer-section__three-img2' src={OfferShape2Section3} alt="Ozdobny ksztat"/>
                        <p data-aos="fade-up">Warsztaty kierowane s do firm, instytucji, ktre kieruj si <br></br> odpowiedzialnoci za swoich pracownikw i dbajc o zrwnowaony <br></br> rozwj, dbaj o czowieka, aby jego praca przynosia mu zadowolenie <br></br> i satysfakcj a take bya zgodna z jego systemem wartoci.</p>
                    </div>
                </div>
                <div className="offer__interval interval-three"></div>
                <div className="offer__offer-section offer-section-four">
                    <img className='offer-section__four-mobile-img1 mobile-img' src={MobileOfferShape1Section4} alt="Ozdobny ksztat"/>
                    <img className='offer-section__four-img1' src={OfferShape1Section4} alt="Ozdobny ksztat"/>
                    <img className='offer-section__four-img2' src={OfferShape2Section4} alt="Ozdobny ksztat"/>
                    <h1 className="offer-section__header">WARSZTATY "SZCZLIWE RODZICIELSTWO"</h1>
                    <div data-aos="fade-right" className="offer-section__logo">
                        <img src={Logo4} alt="Ozdobne logo"/>
                    </div>
                    <div data-aos="fade-down" className="offer-section__p-one">
                        <p className="p-one__header">WARSZTATY DLA RODZICW</p>
                    </div>
                    <div data-aos="fade-left" className="offer-section__list">
                        <p>Cele:</p>
                        <ul>
                            <li>warto wasna  i pewno siebie u dzieci,</li>
                            <li>pewno siebie jako oglny koncept, stanowicy podstaw w wiadomym rodzicielstwie,</li>
                            <li>komunikacja interpersonalna w rodzinie,</li>
                            <li>znaczenie emocji czonkw rodziny, ktrych zrozumienie prowadzi do lepszej komunikacji,</li>
                            <li>przekonania dotyczce idealnego rodzicielstwa, ktre nie istnieje,</li>
                            <li>praca nad pozytywnym budowaniem relacji w rodzinie,</li>
                            <li>tworzenie rodzinnych gier planszowych,</li>
                            <li>praca nad znaczeniem akceptacji i wiary w dziecko,</li>
                            <li>stworzenie przestrzeni wymiany dowiadcze, wzajemnego wsparcia w oparciu o zaufanie.</li>
                        </ul>
                    </div>
                    <div className="offer-section__dot"></div>
                    <p data-aos="fade-up" className="offer-section__p-two">Kurs zakada 9 spotka, <br></br> kade trwajce trzy godziny.</p>
                    <div data-aos="fade-up" className="offer-section__p-three">
                        <p className="p-three__header">Spotkania bd prowadzone warsztatowo, aby kady wiadomy rodzic mg w praktyce dowiadcza pozyskiwan wiedz. </p>
                    </div>
                </div>
                <div className="offer__interval interval-four"></div>
                <div className="offer__offer-section offer-section-five">
                    <img className='offer-section__five-mobile-img1 mobile-img' src={MobileOfferShape1Section5} alt="Ozdobny ksztat"/>
                    <img className='offer-section__five-img1' src={OfferShape1Section5} alt="Ozdobny ksztat"/>
                    <h1 className="offer-section__header">WARSZTATY "W POSZUKIWANIU PEWNOCI SIEBIE"</h1>
                    <div data-aos="fade-left" className="offer-section__logo">
                        <img src={Logo5} alt="Ozdobne logo"/>
                    </div>
                    <div data-aos="fade-down" className="offer-section__p-one">
                        <p className="p-one__header">WARSZTATY DLA MODZIEY 14+</p>
                    </div>
                    <div data-aos="fade-right" className="offer-section__list">
                        <p>Cele:</p>
                        <ul>
                            <li>ksztatowanie poczucia wasnej wartoci,</li>
                            <li>pewnoci siebie, odpowiedzialnoci za wasne decyzje,</li>
                            <li>umiejtnoci odmawiania, radzenia sobie z trudnymi sytuacjami <br></br> w szkole i grupie rwieniczej,</li>
                            <li>poprawa komunikacji w relacjach z najbliszymi,</li>
                            <li>ksztatowanie postaw sprzyjajcych rozwijaniu wasnego potencjau,</li>
                            <li>stworzenie konstruktywnego i stabilnego systemu wartoci,</li>
                            <li>rozbudzanie wiary we wasne siy i moliwoci.</li>
                        </ul>
                    </div>
                    <p data-aos="fade-up" className="offer-section__p-two">Warsztaty bd odbyway si co dwa tygodnie przez 7 miesicy. Czas trwania jednego spotkania  3 godziny. Liczebno grupy 8-10 osb.</p>
                </div>
                <div className="offer__projects">
                    <a href="" className="projects__project"><img src={ProjectLogo1} alt="Logo projektu Brainy"/></a>
                    <a href="//osadawiedzy.pl" className="projects__project"><img src={ProjectLogo2} alt="Logo projektu Osada Wiedzy"/></a>
                    <a href="" className="projects__project"><img src={ProjectLogo3} alt="Logo projektu Still Up"/></a>
                    <a href="" className="projects__project"><img src={ProjectLogo4} alt="Logo projektu Wild Women"/></a>
                </div>
            </div>
        </>
    )
}

export default Offer
